import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  code: PropTypes.string.isRequired,
  timestamp: PropTypes.string,
  country: PropTypes.string,
  enable: PropTypes.bool,
  idMachineType: PropTypes.number,
});

export const formValues = PropTypes.shape({
  code: PropTypes.string,
  timestamp: PropTypes.oneOfType([PropTypes.string, PropTypes.instanceOf(Date)]),
  country: PropTypes.string,
  enable: PropTypes.bool,
  idMachineType: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
});

export const formTouched = PropTypes.shape({
  code: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  timestamp: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  country: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  enable: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  idMachineType: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  code: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  timestamp: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  country: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  enable: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  idMachineType: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
