import { getDefaultOptions, request } from 'api/helpers';

const resource = 'machines';

export const apiMachineGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiMachinePost = async (serviceUrl, machine) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: machine ? JSON.stringify(machine) : null,
  };
  return request(url, options);
};

export const apiMachinePut = async (serviceUrl, machine) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: machine ? JSON.stringify(machine) : null,
  };
  return request(url, options);
};

export const apiMachineDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
